import React, { Children, cloneElement, forwardRef, useRef, useState } from 'react';
import { array, bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-accordion';

const Accordion = forwardRef(({
  allowMultipleExpanded,
  children,
  className,
  defaultExpandedId,
  id = shortid.generate(),
  onChange,
  ...remainingProps
}, ref) => {
  const [expandedId, setExpandedId] = useState(!allowMultipleExpanded ? defaultExpandedId.slice(0,1) : defaultExpandedId);
  const [focusedId, setFocusedId] = useState();
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  const updateFocusedId = (e, index, count) => {
    const code = e.charCode || e.keyCode;

    if (code === 35) {
      e.preventDefault();
      setFocusedId(count - 1);
    }

    if (code === 36) {
      e.preventDefault();
      setFocusedId(0);
    }

    if (code === 38) {
      e.preventDefault();
      setFocusedId(index - 1 < 0 ? count - 1 : index - 1);
    }

    if (code === 40) {
      e.preventDefault();
      setFocusedId((index + 1) % count);
    }
  };

  const updateExpandedArray = (e, index) => {
    let ids = [...expandedId];
    const accordionIndex = ids.findIndex(i => i === index);

    if (accordionIndex === -1 && Number.isInteger(index)) {
      ids = allowMultipleExpanded ? [...ids, index] : [index];
    }

    if (accordionIndex > -1) {
      ids = [...ids.slice(0, accordionIndex), ...ids.slice(accordionIndex + 1)];
    }

    setExpandedId(ids);
    if (typeof onChange === 'function') {
      onChange(index);
    }
  };

  return (
    <div
      className={classnames(CSS_PREFIX, className)}
      id={uid}
      ref={ref}
      {...remainingProps}
    >
      {
        Children.map(children, (child, k) =>  cloneElement(child, {
          expanded: expandedId.includes(k),
          focus: k === focusedId,
          id: `${k}--${uid}`,
          index: k,
          onClick: (e, index) => updateExpandedArray(e, index),
          onKeyDown: (e, index) => updateFocusedId(e, index, Children.count(children))
        }))
      }
    </div>
  );
});

Accordion.propTypes = {
  /**
  * Allow multiple expanded
  */
  allowMultipleExpanded: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Array of panel indexes to initially show as expanded
  */
  defaultExpandedId: array,

  /**
  * disabled
  */
  disabled: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
  * On Change fn
  */
  onChange: func
};

Accordion.defaultProps = {
  defaultExpandedId: []
};


Accordion.displayName = 'Accordion';

export default Accordion;
