import React, { Children, cloneElement, forwardRef } from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-list';

const List = forwardRef((props, ref) => {
  const {
    children,
    className,
    type,
    ...remainingProps
  } = props;

  const Tag = type === 'ordered' ? 'ol' : 'ul';

  return (
    <Tag
      className={classnames(CSS_PREFIX, {
        [`${CSS_PREFIX}--${type}`]: Boolean(type)
      }, className )}
      ref={ref}
      {...remainingProps}
    >
      {
        Children.map(children, child => {
          if (child) {
            return cloneElement(child, {
              className: classnames(child.props.className, `${CSS_PREFIX}-item`)
            });
          }

          return child;
        })
      }
    </Tag>
  );
});

List.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Type
   */
  type: oneOf(['ordered'])
};

List.displayName = 'List';

export default List;
